import React from "react"
import Link from "../../components/Link"
import * as PropTypes from "prop-types"
import S from 'string'
import css from './GridListItem.module.css'


const GridListItem = ({data}) => {
  
  return (
    <li className={css['grid-list-item']}>
      <h2>{data.title}</h2>
      <img src={data.media[0].file.url} alt={data.media[0].title} />
      <Link to={`/work/${S(data.title).slugify().s}`}>{data.title}</Link>
    </li>
  )
}

GridListItem.propTypes  = {
  data: PropTypes.object.isRequired
}

export default GridListItem


