import React from "react"


import * as PropTypes from "prop-types"
import { rhythm } from "../../utils/typography"
import S from 'string'
import Title from "../../components/Title"
import ContentContainer from "../../components/ContentContainer";
import GridList from '../../components/GridList'

const propTypes = {
  data: PropTypes.object.isRequired,
}


class AboutPage extends React.Component {

  render() {

    return (
      
      <div >
        <ContentContainer>
          <Title>About</Title>
          <p>Ahhhhh, the old describe your self exercise, the hardest work one can do.</p>
          <p>Ok, here goes ...</p>
          <p>I've been working as a front end developer for the last 12 years. What that means is, I have seen the javascript library of choice go from prototype / scriptaculous to jquery with sprinklings of mootools, to backbone to some brief flirtations with Angular to the current battle of React and Vue.</p> 
          <p>I have worked in many locations around the world including Amsterdam, London, Madrid, Moscow and Minsk but my favourite of all is my home Toronto, we just need to work on the coffee here ...</p>
          <p>I started my development life in agency land making websites that were commercials for products I didn't care about, that got old fast.</p>
          <p>I then moved on to consulting in the e-commerce world where I got to develop a product and implement it for major clients in London, this was a great time.</p>
          <p>I'm now working on product at Shopify and Loving it.</p>
          <p>The story continues ... </p>
        </ContentContainer>
        
      </div>

    )
  }
}



export default AboutPage





